.campaign-table {
  width: 100%;
  border-collapse: collapse;
}

.campaign-table th,
.campaign-table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

.campaign-table th {
  background-color: #3498db;
  color: #fff;
}

.campaign-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.delete-btn {
  padding: 5px 10px;
  background-color: #e74c3c;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.delete-btn:hover {
  background-color: #c0392b;
}
